<template>
    <div id="addProject">
        <el-row>
            <el-col :span='24'>
                <el-card shadow="always">
                    <div class="title"><span class="iconfont icon-icon-up"></span>添加诊改年度规划</div>
                    <el-form :model="create" :rules="rules" ref="create" class="demo-ruleForm">
                        <div class="form_item">
                            <el-form-item label="规划名称：" prop="mc" required style="width:45%">
                                <el-input v-model="create.mc" 
                                type="textarea" 
                                resize="none" 
                                autosize 
                                placeholder="请输入规划名称">
                                </el-input>
                            </el-form-item>
                            <el-form-item label="所属层面：" prop="mc" required style="width:40%">
                                <el-select v-model="create.cengmian" 
                                placeholder="请选择项目重要程度" 
                                style="width:100%;">
                                    <el-option
                                    v-for="item in option3"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </div>
                        <div class="form_item">
                            <el-form-item label="负责人：" class="fzr">
                                <el-col :span="12" style="width:100%;padding:0;">
                                    <el-autocomplete
                                    class="inline-input"
                                    v-model="create.fzr"
                                    :fetch-suggestions="queSearch"
                                    placeholder="请选择负责人"
                                    @select="fzr"
                                    clearable>
                                    </el-autocomplete>
                                </el-col>
                            </el-form-item>
                            <el-form-item label="经费预算：" class="jfys" prop="jfys">
                                <el-input v-model="create.jfys" 
                                type="number" placeholder="请输入金额" autocomplete="off">
                                    <template slot="append">万元</template>
                                </el-input>
                            </el-form-item>
                            <el-form-item label="紧急程度：" class="important" prop="important">
                                <el-select v-model="create.important" 
                                placeholder="请选择项目重要程度" 
                                style="width:100%;">
                                    <el-option
                                    v-for="item in option2"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </div>
                        <div class="form_item">
                            <el-form-item label="开始时间：" class="starrttime" prop="start">
                                <el-date-picker
                                    v-model="create.start"
                                    type="date"
                                    placeholder="选择开始日期">
                                </el-date-picker>
                            </el-form-item>
                            <el-form-item label="结束时间：" class="endtime" prop="end">
                                <el-date-picker
                                    v-model="create.end"
                                    type="date"
                                    placeholder="选择结束日期">
                                </el-date-picker>
                            </el-form-item>
                        </div>
                        <el-form-item label="参与人：" prop="cyr">
                            <el-select 
                            v-model="create.cyr" 
                            multiple 
                            filterable 
                            placeholder="请选择参与人" 
                            style="width:100%;">
                                <el-option v-for="item in options" 
                                :key="item.value" 
                                :label="item.label" 
                                :value="item.value" 
                                :disabled="item.label==create.fzr?true:false">
                                </el-option>
                            </el-select>
                        </el-form-item>
                        <div class="khbz">
                            <el-form-item label="考核指标：" style="width:33%;margin-right:35px;" prop="khzb">
                                <el-col :span="12" style="width:100%;padding:0;">
                                    <el-autocomplete
                                    class="inline-input"
                                    v-model="create.khzb"
                                    :fetch-suggestions="querySearch"
                                    placeholder="请输入内容"
                                    @select="handleSelect"
                                    clearable>
                                    </el-autocomplete>
                                </el-col>
                            </el-form-item>
                            <el-form-item label="诊改目标：" style="flex:1;" prop="zgmb">
                                <div class="zhengaimubiao">
                                    <el-select v-model="create.mark" class="mb_mark" placeholder="请选择">
                                        <el-option
                                        v-for="item in option1"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value">
                                        </el-option>
                                    </el-select>
                                    <el-input class="mb_ipt1" v-model="create.zgmb" placeholder="请输入目标" autocomplete="off"></el-input>
                                </div>
                            </el-form-item>
                        </div>
                        <el-form-item label="规划简介：" prop="jj">
                            <el-input v-model="create.jj" type="textarea" :autosize="{ minRows: 6}" placeholder="请输入简介内容"></el-input>
                        </el-form-item>
                    </el-form>
                    <div class="add-footer">
                        <el-button type="primary" @click="set(create)">创 建</el-button>
                        <el-button @click="back()">返 回</el-button>
                    </div>
                </el-card>
            </el-col>
        </el-row>
    </div>
</template>

<script>
import { setTimeout } from 'timers';
export default {
    name: 'add',
    inject:["reload"],
    data() {
        return {
            creator_id: null,
            create: {
                cengmian: '0',
                level: '1',
                id: '',
                mc:'',
                important: '2',
                start: '',
                end: '',
                jfys: null,
                fzr: '',
                fid:'',
                cyr: [],
                khzb: '',
                khzbId: '',
                zgmb: '',
                mark: '',
                jd: 0,
                jj: '',
            },
            rules: {
                mc: [
                    { required: true, message: '请输入规划名称', trigger: 'blur' }
                ],
                fzr: [
                    { required: true, message: '请选择负责人', trigger: 'change' }
                ],
                jfys: [
                    { required: true, message: '请输入金额', trigger: 'blur' }
                ],
                start: [
                    { required: true, message: '请选择开始时间', trigger: 'change' }
                ],
                end: [
                    { required: true, message: '请选择结束时间', trigger: 'change' }
                ],
                cyr: [
                    { required: true, message: '请选择参与人', trigger: 'change' }
                ],
                khzb: [
                    { required: true, message: '请输入考核指标', trigger: 'change' }
                ],
                zgmb: [
                    { required: true, message: '请输入诊改目标', trigger: 'change' }
                ],
                jj: [
                    { required: true, message: '请输入规划简介', trigger: 'blur' }
                ]
            },
            options:[],     //负责人参与人选择器
            option1: [{
                value: '1',
                label: '＞'
            },{
                value: '2',
                label: '≥'
            },{
                value: '3',
                label: '＜'
            },{
                value: '4',
                label: '≤'
            },{
                value: '5',
                label: '＝'
            },{
                value: '6',
                label: '≠'
            }],
            option2: [
                {
                    value: '1',
                    label: '重要'
                },{
                    value: '2',
                    label: '普通'
                },{
                    value: '3',
                    label: '暂缓'
                }
            ],
            option3: [
                {
                    value: '0',
                    label: '学校层面'
                },{
                    value: '1',
                    label: '专业层面'
                },{
                    value: '2',
                    label: '课程层面'
                }
            ],
            restaurants1:[],
            restaurants:[],
            
        }
    },
    methods: {
        querySearch(queryString, cb) {
            var restaurants = this.restaurants;
            var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
            // 调用 callback 返回建议列表的数据
            cb(results);
        },
        createFilter(queryString) {
                return (restaurant) => {
                return (restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
            };
        },
        queSearch(queryString, cb) {
            var restaurants1 = this.restaurants1;
            var results = queryString ? restaurants1.filter(this.createFilter(queryString)) : restaurants1;
            // 调用 callback 返回建议列表的数据
            cb(results);
        },
        fzr(e){
            localStorage.setItem("fid",e)
            this.create.fid=e.id;
            console.log(e.id)
        },
        loadAll() {
            let creator_id = this.$route.query.creator_id;
            this.creator_id = creator_id;
            // var asd = localStorage.getItem('userId');
            // console.log(asd, 'asdasdasdasd')
            // console.log(window.localStorage.getItem('userId'), 'userId - creat_id')
            var khzb = [];
            var fzyli=[];
            var _this = this
            $.ajax({
                type: "post",
                url: _this.baseUrl+"/ydjxzg/mb/totargetadd.action",
                data: {},
                dataType: "json",
                success: function (res) {
                    console.log(res)
                    if(res.khzb){
                        for(var i = 0;i < res.khzb.length;i++){
                            khzb.push({id:res.khzb[i].id,value:res.khzb[i].zbmc})
                        }
                        // console.log(khzb)
                    }
                    if(res.userlist){
                        for(var i = 0;i < res.userlist.length;i++){
                            fzyli.push({id:res.userlist[i].user_id,value:res.userlist[i].name})
                        }
                        console.log(fzyli)
                        for(var j = 0;j < res.userlist.length;j++){
                            _this.options.push({value:res.userlist[j].user_id,label:res.userlist[j].name})
                            
                        }
                        
                    }
                }
            });
            this.restaurants = khzb
             this.restaurants1 = fzyli;
            // return khzb;
        },
        handleSelect(item) {
            // console.log(item);
            this.create.khzbId = item.id
        },
        back(){
            this.$router.go(-1)
        },
        set(formName){
            console.log(this.create.cyr, '参与人');
            console.log(this.creator_id, '参与人');
            //将this.create里的数据传给后台接口创建项目规划
            if(this.create.start && this.create.end){
                var d_start = new Date(this.create.start)
                var d_end = new Date(this.create.end)
                var s_m = d_start.getMonth()
                var s_d = d_start.getDate()
                var e_m = d_end.getMonth()
                var e_d = d_end.getDate()
                console.log(s_m,s_d)
                if(s_m + 1 < 10){
                    s_m = "0" + (s_m + 1)
                }else{
                    s_m = s_m + 1
                }
                if(s_d < 10){
                    s_d = "0" + s_d
                }
                if(e_m + 1 < 10){
                    e_m = "0" + (e_m + 1)
                }else{
                    e_m = e_m + 1
                }
                if(e_d < 10){
                    e_d = "0" + e_d
                }
                this.create.start = d_start.getFullYear() + '-' + s_m + '-' + s_d
                this.create.end = d_end.getFullYear() + '-' + e_m + '-' + e_d
            }
            this.create.cyr = this.create.cyr.toString();
            
            console.log(formName)
            // var creator_id = localStorage.getItem('userId');
            // console.log(localStorage.getItem('userId'))
            if(this.create.mc && this.create.start && this.create.end && this.create.jfys && this.create.fzr && this.create.khzbId && this.create.zgmb && this.create.mark && this.create.jj){
                var _this = this
                $.ajax({
                    type: "post",
                    url: _this.baseUrl+"/ydjxzg/mb/addtarget.action",
                    data: {
                        'creator_id': _this.creator_id,
                        'target_category': formName.cengmian,
                        'target_name':formName.mc, 
                        'manager_id':this.create.fid, 
                        'funds':formName.jfys, 
                        'importancedj':formName.important, 
                        'start_time':formName.start, 
                        'end_time':formName.end, 
                        'cyr':formName.cyr, 
                        'target_stand':formName.khzbId, 
                        'target_stand_contion':formName.mark, 
                        'target_stand_info':formName.zgmb, 
                        'target_info':formName.jj 
                    },
                    dataType: "json",
                    success: function (res) {
                        var bb = [];
                        for (let w = 0; w < _this.create.cyr.length; w++) {
                            if (_this.create.cyr[w] != ",") {
                                bb.push(_this.create.cyr[w]);
                            }  
                        }
                        _this.create.cyr = bb;
                        console.log(res)
                        if(res.mess){
                            _this.$message.success('创建成功！')
                            _this.$router.push({path: "/zgmb"})
                            setTimeout(function(){
                                _this.reload();
                            },500)
                        }else{
                            _this.$message.error('创建规划失败！')
                        }
                    },
                    error: function(res){
                        var cc = [];
                        for (let e = 0; e < _this.create.cyr.length; e++) {
                            if (_this.create.cyr[e] != ",") {
                                cc.push(_this.create.cyr[e]);
                            }  
                        }
                        _this.create.cyr = cc;
                        _this.$message.error('创建失败！如有疑问，请联系管理员')
                    }
                });
            }else{
                // var cyr01 = this.create.cyr.split(",");
                // var aa = this.create.cyr;
                // aa.split(",");
                var aa = [];
                for (let q = 0; q < this.create.cyr.length; q++) {
                    if (this.create.cyr[q] != ",") {
                        aa.push(this.create.cyr[q]);
                    }  
                }
                // console.log(aa, '这是aa')
                this.create.cyr = aa;
                this.$message.error('请完善诊改年度规划！')
                // console.log(this.create.cyr, 'ceshi')
            }
            
            // this.$refs[formName].validate((valid) => {
            //     if (valid) {
            //         this.$message({
            //             message: '创建成功!',
            //             type: 'success'
            //         });
            //     } else {
            //         console.log('error submit!!');
            //         return false;
            //     }
            // });
            
        }
    },
    mounted() {
        this.loadAll()
        
    },
}
</script>

<style>
#addProject{
    width: 100%;
    height: 100%;
    margin-top: 20px;
}
#addProject .el-row{
    padding: 0 10px;
}
#addProject .el-row .el-card__body{
    padding: 50px 8%;
}
#addProject .title{
    width: 100%;
    height: auto;
    line-height: 40px;
    font-size: 24px;
    margin: 0 auto;
}
#addProject .title .iconfont{
    margin-left: -16px;
}
#addProject .el-form{
    /* padding: 0 8%; */
}
#addProject .form_item{
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
/* @media all and (max-width: 1300px) {
    #addProject .form_item .starrttime,#addProject .form_item .endtime,#addProject .form_item .fzr,#addProject .form_item .jfys{
        width: 46%;
    }
} */
#addProject .form_item .fzr,#addProject .form_item .jfys,#addProject .form_item .important{
    width: 30%;
}
#addProject .form_item .starrttime,#addProject .form_item .endtime{
    width: 45%;
}
#addProject .form_item .el-date-editor{
    width: 100%;
}
#addProject .el-form-item .el-form-item__label{
    display: block;
    float: none;
    text-align: left;
}
#addProject .inline-input{
    width: 100%!important; 
}
#addProject .zhengaimubiao{
    width: 100%;
    display: flex;
    justify-content: space-between;
}
#addProject .khbz{
    width: 100%;
    display: flex;
    justify-content: space-between;
}
#addProject .mb_mark{
    width: 49%;
    margin-right:15px;
}
#addProject .mb_ipt1{
    width: 49%;
}
#addProject .iconfont{
    font-weight: 900;
}
#addProject .add-footer{
    width: 100%;
}
#addProject .add-footer .el-button--default{
    background: #fdfdfd;
}
#addProject .btn-custom-cancel {
    float: right;
    margin-left: 10px;
}
#addProject .el-form-item__label:before{
    display: none;
}
body .el-table th.gutter{
    display: table-cell!important;
}
input::-webkit-outer-spin-button,  
input::-webkit-inner-spin-button{  
    -webkit-appearance: none !important;  
    margin: 0;  
}
</style>



